@import url('https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@700&display=swap');

:root {
  --main-bg-color: #F1F1F1;
  --main-text-color: #A0A0A0;
  --blue-accent-color: #58B5C3;
  --red-accent-color: #D8543D;
  --yellow-accent-color: #E6B525;
  --tan-accent-color: #f8eebc;
  --green-accent-color: #19ad5e;
}

body {
  background-color: var(--green-accent-color);
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='20' viewBox='0 0 100 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.184 20c.357-.13.72-.264 1.088-.402l1.768-.661C33.64 15.347 39.647 14 50 14c10.271 0 15.362 1.222 24.629 4.928.955.383 1.869.74 2.75 1.072h6.225c-2.51-.73-5.139-1.691-8.233-2.928C65.888 13.278 60.562 12 50 12c-10.626 0-16.855 1.397-26.66 5.063l-1.767.662c-2.475.923-4.66 1.674-6.724 2.275h6.335zm0-20C13.258 2.892 8.077 4 0 4V2c5.744 0 9.951-.574 14.85-2h6.334zM77.38 0C85.239 2.966 90.502 4 100 4V2c-6.842 0-11.386-.542-16.396-2h-6.225zM0 14c8.44 0 13.718-1.21 22.272-4.402l1.768-.661C33.64 5.347 39.647 4 50 4c10.271 0 15.362 1.222 24.629 4.928C84.112 12.722 89.438 14 100 14v-2c-10.271 0-15.362-1.222-24.629-4.928C65.888 3.278 60.562 2 50 2 39.374 2 33.145 3.397 23.34 7.063l-1.767.662C13.223 10.84 8.163 12 0 12v2z' fill='%23f1f1f1' fill-opacity='0.12' fill-rule='evenodd'/%3E%3C/svg%3E");
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5 {
  font-weight: 700;
  font-family: 'Zilla Slab', serif;
}

.background {
  background-color: var(--main-bg-color);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  padding: 2% 5%;
  margin: 15px 0;
  height: 100%;
}



#logo-pencil {
  fill: var(--tan-accent-color);
  width: 30px;
  height: 30px;
  padding-right: 10px;
}

.brand-logo {
  padding-left: 20px !important;
  font-family: 'Zilla Slab', serif;
  font-weight: 700;
}

nav ul a {
  font-weight: 600;
  font-size: 1.1rem !important;
}

.mr-10 {
 margin-right: 10px;
}


.capitialize {
  text-transform: capitalize;
}

.uppercase {
  text-transform: uppercase;
}

.clickable {
  cursor: pointer;
}


.error-message {
  background-color: var(--red-accent-color);
  color: white;
  font-size: 20px;
  padding: 10px;
  margin: 0 0 10px 0;
  border-radius: 5px;
}

.input-field label, .custom-label {
  color: var(--blue-accent-color) !important;
}